import { Injectable } from '@angular/core';
import API, { graphqlOperation } from '@aws-amplify/api';
import { searchUsers, searchJobOffers, searchAcademicPrograms, searchOrganizations } from 'src/graphql/queries';
import { Constants } from '../@global/constants';
@Injectable({
  providedIn: 'root',
})
export class SearchService {
  constructor() {}

  public async searchPeople(text: string) {
    return API.graphql(graphqlOperation(searchUsers, { text, limit: Constants.SEARCH_LIMIT }));
  }

  public async searchOrganizations(text: string) {
    return API.graphql(graphqlOperation(searchOrganizations, { text, limit: Constants.SEARCH_LIMIT }));
  }

  public async searchOpportunities(text: string) {
    return API.graphql(graphqlOperation(searchJobOffers, { text, limit: Constants.SEARCH_LIMIT }));
  }

  public async searchPrograms(text: string) {
    return API.graphql(graphqlOperation(searchAcademicPrograms, { text, limit: Constants.SEARCH_LIMIT }));
  }
}
