import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { TranslateService } from '@ngx-translate/core';
import { NavigationExtrasService } from '../@core/navigation-extras.service';
import { SearchService } from '../@core/search.service';
import { forkJoin, from } from 'rxjs';
import { take } from 'rxjs/operators';
import { UserPublicInfo } from 'src/API';
import { Utils } from '../@global/utils';
import { UtilsService } from '../@core/utils.service';
import { PUBLISHER_TYPE } from '../@shared/enums/publisher-type';
import { Store } from '@ngxs/store';
import { UserState } from '../@store/user/user.state';
import { IJobOffer } from '../@store/job-offers/job-offers.model';
import { SetCurrentJobOffer } from '../@store/job-offers/job-offers.actions';
import { Router } from '@angular/router';
import { IAcademicProgram } from '../@store/academic-programs/academic-programs.model';
import { SetCurrentAcademicProgram } from '../@store/academic-programs/academic-programs.actions';
import { IonSearchbar } from '@ionic/angular';
import { Constants } from '../@global/constants';
import { IOrganizationInfo } from '../@shared/interfaces/organization-info';
import { InformService } from '../@core/inform.service';

@Component({
  selector: 'app-search',
  templateUrl: './search.page.html',
  styleUrls: ['./search.page.scss'],
})
export class SearchPage implements OnInit {
  @ViewChild('searchbar', { static: false }) searchbar: IonSearchbar;
  public extraPlaceholder = '';
  public usersSearched: UserPublicInfo[] = [];
  public organizationsSearched: IOrganizationInfo[] = [];
  public opportunitiesSearched: IJobOffer[] = [];
  public academicProgramsSearched: IAcademicProgram[] = [];
  public debounce = Constants.DEBOUNCE_SEARCH;
  public showSpinner = false;

  private origin = '';
  private userId = '';

  constructor(
    private location: Location,
    private navigationExtrasService: NavigationExtrasService,
    private translateService: TranslateService,
    private searchService: SearchService,
    private utilsService: UtilsService,
    private store: Store,
    private router: Router,
    private informService: InformService
  ) {}

  ionViewWillEnter() {
    this.searchbar.setFocus();
  }

  ngOnInit() {
    this.userId = this.store.selectSnapshot(UserState.getUserInfo).id;

    if (this.navigationExtrasService.getExtras()?.origin) {
      // /timeline
      const originUrl = this.navigationExtrasService.getExtras().origin.split('/');
      const origin = originUrl[originUrl.length - 1];
      this.origin = origin;
      switch (origin) {
        case 'opportunities':
          this.extraPlaceholder = this.getTranslationKey('opportunities');
          break;
        case 'academics':
          this.extraPlaceholder = this.getTranslationKey('academics');
          break;
        default:
          this.extraPlaceholder = this.getTranslationKey('networking');
        // case networking:
        // case timeline:
        // case notifications:
      }
    }
  }

  public clear() {
    this.usersSearched = [];
    this.organizationsSearched = [];
    this.opportunitiesSearched = [];
    this.academicProgramsSearched = [];
  }

  public search($event) {
    const val = $event.target.value;
    if (val === '') {
      this.clear();
    }
    if (val.length >= 3) {
      switch (this.origin) {
        case 'opportunities':
          this.searchOpportunities(val);
          break;
        case 'academics':
          this.searchPrograms(val);
          break;
        default:
          this.searchUsersAndOrganizations(val);
      }
    }
  }

  private searchUsersAndOrganizations(text: string) {
    this.showSpinner = true;
    forkJoin([from(this.searchService.searchPeople(text)), from(this.searchService.searchOrganizations(text))])
      .pipe(take(1))
      .subscribe((results) => {
        this.usersSearched = this.mapItemsPhoto(results[0]['data'].searchUsers || []);
        this.organizationsSearched = this.mapItemsPhoto(results[1]['data'].searchOrganizations || []);
        this.usersSearched = this.usersSearched.filter((user) => user.id !== this.userId);
        this.showSpinner = false;
        if (this.usersSearched.length === 0 && this.organizationsSearched.length === 0) {
          this.informService.showWarning('search.noResults');
        }
      });
  }

  private searchOpportunities(text: string) {
    this.showSpinner = true;
    from(this.searchService.searchOpportunities(text))
      .pipe(take(1))
      .subscribe((response) => {
        this.opportunitiesSearched = this.mapOpportunities(response['data'].searchJobOffers) || [];
        this.showSpinner = false;
        if (this.opportunitiesSearched.length === 0) {
          this.informService.showWarning('search.noResults');
        }
      });
  }

  private searchPrograms(text: string) {
    this.showSpinner = true;
    from(this.searchService.searchPrograms(text))
      .pipe(take(1))
      .subscribe((response) => {
        this.academicProgramsSearched = this.mapPrograms(response['data'].searchAcademicPrograms) || [];
        this.showSpinner = false;
        if (this.academicProgramsSearched.length === 0) {
          this.informService.showWarning('search.noResults');
        }
      });
  }

  private getTranslationKey(key) {
    return this.translateService.instant('search.' + key);
  }

  public goBack(): void {
    this.location.back();
  }

  public itemClick($event) {
    this.utilsService.goProfile($event.model.id, !$event.isOrganization ? PUBLISHER_TYPE.USER : 0);
  }

  public opportunityClick($event) {
    const opportunity = this.opportunitiesSearched.find((op) => op.id === $event);
    const opportunityUpdated: IJobOffer = { ...opportunity, own: false } as IJobOffer;
    this.store.dispatch(new SetCurrentJobOffer(opportunityUpdated));
    this.router.navigate(['opportunity-detail', $event]);
  }

  public academicProgramClick($event) {
    const program = this.academicProgramsSearched.find((prog) => prog.id === $event);
    const programUpdated: IAcademicProgram = { ...program, own: false } as IAcademicProgram;
    this.store.dispatch(new SetCurrentAcademicProgram(programUpdated));
    this.router.navigate(['academic-program-detail', $event]);
  }

  private mapItemsPhoto(items) {
    return items.map((item) => {
      const photo = Utils.mapCloudfrontProfilePhoto(item.photo);
      return {
        ...item,
        photo,
      };
    });
  }

  private mapOpportunities(opportunities) {
    return opportunities.map((opportunity) => {
      return {
        ...opportunity,
        photo: Utils.mapCloudfrontProfilePhoto(opportunity.photo),
        creation_date_literal: this.utilsService.calculateCreatedLiteral(Date.now(), opportunity.creation_date),
      };
    });
  }

  private mapPrograms(programs) {
    return programs.map((program) => {
      return {
        ...program,
        photo: Utils.mapCloudfrontProfilePhoto(program.photo),
        location: Utils.generateUserFriendlyLocation(
          program.country,
          program.region,
          program.city,
          program.province,
          program.region_name
        ),
        friendly_start_date: Utils.getDateMMMYY(program.start_date),
        creation_date_literal: this.utilsService.calculateCreatedLiteral(Date.now(), program.creation_date),
        friendly_end_date: Utils.getDateMMMYY(program.end_date),
      };
    });
  }
}
