import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { UserPublicInfo } from 'src/app/networking/models/user-public-info.model';

import { Utils } from '../../../@global/utils';
import { IOrganizationInfo } from '../../../@shared/interfaces/organization-info';

@Component({
  selector: 'app-item-user-org-search',
  templateUrl: './item-user-org-search.component.html',
  styleUrls: ['./item-user-org-search.component.scss'],
})
export class ItemUserOrgSearchComponent implements OnInit, OnChanges {
  @Input() info: UserPublicInfo | IOrganizationInfo;
  @Input() isOrganization = false;
  @Output() actionButtonClick = new EventEmitter<{ id: string; isOrganization: boolean }>();
  @Output() actionItemClick = new EventEmitter<{
    model: UserPublicInfo | IOrganizationInfo;
    isOrganization: boolean;
  }>();
  public hasLocation: boolean;
  public userLocation: string;
  public prefix: string;

  constructor() {}

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges) {
    if (changes['info']?.currentValue) {
      this.hasLocation = !!this.info.country;

      if (this.hasLocation) {
        this.userLocation = Utils.generateUserFriendlyLocation(
          this.info.country,
          this.info.region,
          this.info.city,
          this.info.province,
          this.info.region_name
        );
      }
      this.prefix = this.isOrganization ? Utils.getSubtypeTranslationPrefix(this.info['organization_type']) : '';
    }
  }

  public onButtonClick(): void {
    this.actionButtonClick.emit({ id: this.info.id, isOrganization: this.isOrganization });
  }

  public onItemClick(): void {
    this.actionItemClick.emit({
      model: this.info,
      isOrganization: this.isOrganization,
    });
  }
}
